import { BASE_URL } from 'variables';
import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';

axios.defaults.baseURL = `${BASE_URL}/api`;

const token = {
  set(token) {
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
  unset() {
    axios.defaults.headers.common.Authorization = '';
  },
};

/*
 * POST @ /users/signup
 * body: { name, email, password }
 *       HTTP-
 */
const register = createAsyncThunk('/auth/sign-up', async (credentials, thunkAPI) => {
  try {
    const { data } = await axios.post('/auth/sign-up', credentials);
    token.set(data.token);
    return data;
  } catch (error) {
    return thunkAPI.rejectWithValue(error.message);
  }
});

/*
 * POST @ /users/login
 * body: { email, password }
 *       HTTP-
 */
const logIn = createAsyncThunk('/auth/sign-in', async (credentials, thunkAPI) => {
  try {
    const { data } = await axios.post('/auth/sign-in', credentials);
    token.set(data.token);
    return data;
  } catch (error) {
    return thunkAPI.rejectWithValue(error.message);
  }
});

/*
 * POST @ /users/logout
 * headers: Authorization: Bearer token
 *   ,    HTTP-
 */
const logOut = createAsyncThunk('/auth/sign-out', async (_, thunkAPI) => {
  try {
    await axios.delete('/auth/sign-out');
    token.unset();
  } catch (error) {
    return thunkAPI.rejectWithValue(error.message);
  }
});
/*
 * GET @ /users/current
 * headers:
 *    Authorization: Bearer token
 *
 * 1.      getState()
 * 2.   ,     
 * 3.   ,    HTTP-   
 */
const fetchCurrentUser = createAsyncThunk(
  'auth/refresh',
  async (_, thunkAPI) => {
    const state = thunkAPI.getState();
    const persistedToken = state.auth.token;

    if (persistedToken === null) {
      console.log(' ,   fetchCurrentUser');
      return thunkAPI.rejectWithValue();
    }

    token.set(persistedToken);

    try {
      const { data } = await axios.get('/users/current');
      return data;
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message);
    }
  }
);

const operations = {
  register,
  logOut,
  logIn,
  fetchCurrentUser,
};
export default operations;
